    flex-direction: row;
    justify-content: space-between;
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
}

.tittle-2xl {
  padding-bottom: 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

@media (min-width: 640px) {
  .tittle-2xl {
    font-size: 1.5rem;
    line-height: 2rem;
  }
}

/*
|--------------------------------------------------------------------------
| Home Screen
|--------------------------------------------------------------------------
*/

.main-banner-products {
  margin-top: 1.25rem;
  display: grid;
  height: 100%;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  place-items: center;
  gap: 0.5rem;
}

@media (min-width: 640px) {
  .main-banner-products {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

/*
|--------------------------------------------------------------------------
| Simple Form
|--------------------------------------------------------------------------
*/

.simple-input-underline {
  border-bottom-width: 2px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.simple-input-underline:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.tittle-3xl {
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: center;
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 600;
}

.simple-select {
  border-radius: 0.125rem;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(156, 163, 175, var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgba(243, 244, 246, var(--tw-bg-opacity));
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

